@-webkit-keyframes Keyframes-grow-in {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}

@keyframes Keyframes-grow-in {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}

.Messages {
  color: #888;
  text-align: center;
  font-family: sans-serif;
  font-size: 20px;
  font-size: 42px;
  font-weight: bold;
  padding: 40px;
  -webkit-animation-name: Keyframes-grow-in;
          animation-name: Keyframes-grow-in;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.Messages-player-one {
  font-size: 52px;
  color: #F0595C;
}

.Messages-player-two {
  font-size: 52px;
  color: #4AC2BF;
}


